export function getNerdConstraint(cp: number): NerdConstraint | null {
  let lo = 0;
  let hi = NERD_CONSTRAINTS.length - 1;
  while (lo <= hi) {
    const mid = (lo + hi) >> 1;
    const entry = NERD_CONSTRAINTS[mid];
    if (cp < entry.start) {
      hi = mid - 1;
    } else if (cp > entry.end) {
      lo = mid + 1;
    } else {
      return entry.constraint;
    }
  }
  return null;
}
